import React, { PropsWithChildren } from 'react';
import format from 'date-fns/format';
import { Box, makeStyles, Theme, Typography } from '@material-ui/core';

const useStyles = makeStyles<Theme>(({ spacing, typography, palette }) => ({
  root: {
    paddingLeft: spacing(2),
    marginTop: spacing(2),
    borderLeft: `4px solid ${palette.grey[300]}`,
  },
  titleWrapper: {
    marginBottom: spacing(0.5),
  },
  title: {
    fontWeight: typography.fontWeightBold,
    fontSize: typography.body2.fontSize,
    marginRight: spacing(1),
  },
}));

interface Props {
  title: string;
  startDate: Date;
  endDate?: Date;
}
const History = ({ title, children, startDate, endDate }: PropsWithChildren<Props>) => {
  const styles = useStyles();
  const formattedStartDate = format(startDate, 'yyyy.MM');
  const formattedEndDate = endDate != null ? format(endDate, 'yyyy.MM') : '현재';

  return (
    <Box className={styles.root}>
      <Box display="flex" alignItems="flex-end" className={styles.titleWrapper}>
        <Typography className={styles.title}>{title}</Typography>
        <Typography variant="caption">
          {formattedStartDate} ~ {formattedEndDate}
        </Typography>
      </Box>
      <Typography variant="body2">{children}</Typography>
    </Box>
  );
};

export default History;
