import React, { PropsWithChildren, useCallback } from 'react';
import { Box, Grid, IconButton, makeStyles, Theme, Tooltip, Typography } from '@material-ui/core';
import Image, { FluidObject } from 'gatsby-image';
import * as Icon from 'react-feather';
import { Logger } from 'utils/loggers';
import History from './History';

const useStyles = makeStyles<Theme>(({ spacing, typography, palette, breakpoints }) => ({
  root: {
    marginBottom: spacing(6),
  },
  logo: {
    minWidth: 50,
    marginRight: spacing(2),
    [breakpoints.down('xs')]: {
      display: 'none',
    },
  },
  title: {
    fontWeight: typography.fontWeightBold,
    fontSize: typography.h6.fontSize,
  },
  description: {
    fontSize: typography.caption.fontSize,
    marginBottom: spacing(1),
  },
  role: {
    fontSize: typography.caption.fontSize,
    color: palette.text.secondary,
  },
  linkButton: {
    padding: spacing(1),
    marginLeft: spacing(1),
  },
}));

interface Props {
  logo: FluidObject;
  title: string;
  role: string;
  description: string;
  link: string;
  logger?: Logger;
}
const OrganizationInfo = ({ logo, title, description, role, link, children, logger }: PropsWithChildren<Props>) => {
  const styles = useStyles();

  const handleExternalLinkClick = useCallback(() => {
    logger?.click('click_external_org_link', { target: title });
  }, [logger, title]);

  return (
    <Grid className={styles.root} container>
      <Grid item>
        <Image className={styles.logo} fluid={logo} />
      </Grid>
      <Grid item xs={10}>
        <Box display="flex" alignItems="center">
          <Typography className={styles.title} component="h3">
            {title}
          </Typography>
          <IconButton href={link} target="_blank" onClick={handleExternalLinkClick} className={styles.linkButton}>
            <Tooltip title="더 알아보기">
              <Icon.Link size={14} />
            </Tooltip>
          </IconButton>
        </Box>

        <Typography className={styles.description}>{description}</Typography>
        <Typography className={styles.role}>{role}</Typography>
        <div>{children}</div>
      </Grid>
    </Grid>
  );
};

OrganizationInfo.History = History;

export default OrganizationInfo;
